.header {
    box-sizing: border-box;
    position: fixed;
    top: 0;
    width: 100%;
    height: 57px;
    padding: 0 1em;

    background: #F1F1F1;
    border: 1px solid #F1F1F1;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.b {
    color: #084710;
}

.logotext {
    width: 73px;
    height: 26px;
    

    font-family: 'Roboto Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;

    color: #000000;

}

